.add-task {  &-enter {    opacity: 0;    width: 0;  }  &-enter-active {    width: 230px;    opacity: 1;    transition: all .5s ease;  }  &-enter-done {    width: 230px;  }  &-exit {    opacity: 1;    width: 230px;  }  &-exit-active {    width: 0;    opacity: 0;    transition: all .5s ease;  }}