.container-chips{  position: fixed;  top: 1rem;  right: 1rem;  bottom: 0;  width: 0;  background-color: rgba(0,0,0,.3);  z-index: 1;  @media (max-width: 576px) {    display: flex;    flex-direction: column-reverse;  }  .chip {    width: 300px;    box-shadow: 1px 1px 5px 2px #000000;    color: white;    padding: .5rem .8rem;    border-radius: 5px;    position: relative;    right: 300px;    transition: all .5s ease-in-out;    margin-bottom: .5rem;    @media (max-width: 576px) {      width: calc(100vw - 2rem);      padding: .3rem .4rem;      font-size: .8rem;    }    .icon {      position: absolute;      top: .5rem;      right: .5rem;      box-shadow: none;      padding: 0;    }    &.chip-enter {      right: 0;    }    &.chip-enter-active {      right: 300px;      @media (max-width: 576px) {        right: calc(100vw - 2rem);      }    }    &.chip-enter-done {      right: 300px;      @media (max-width: 576px) {        right: calc(100vw - 2rem);      }    }    &.chip-exit {      right: 300px;      @media (max-width: 576px) {        right: calc(100vw - 2rem);      }    }    &.chip-exit-active {      right: 0;    }    & > h3 {      font-weight: bolder;      letter-spacing: .5px;      text-transform: uppercase;      color: black;    }    & > p {      color: #4d4140;    }    &.success {      background-color: #07a107;    }    &.error {      background-color: firebrick;    }  }}