@import "./frontend/assets/styles/variables";
@import "./frontend/assets/styles/Roboto";
@import "./frontend/assets/styles/scroll";

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html {
  font-size: 14px;
  font-family:  'Roboto', serif, Arial;
}

#root {
  height: 100vh;
  width: 100%;
  background-image: url("frontend/assets/img/cristmas_schwarzwald-min.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.todos {
  width: 100%;
  height: 100%;
  background-color: $colorBackground;
  perspective: 1200px;
  transform-style: preserve-3d;
  overflow: hidden;
  position: relative;

  &_wrap {
    border: 1rem solid rgba(255, 255, 255, 0.2);
    width: 100%;
    height: 100%;
    transform-style: preserve-3d;
    transition: .7s linear;

    &.active-back {
      transform: rotateX(180deg);
    }
  }

  &-front, &-back {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, .5);
    backface-visibility: hidden;
    border-radius: 3px;
    padding: 5.5rem 2rem 2rem;

    @media (max-width: 576px) {
      padding: 5.5rem .7rem .7rem;
    }

    &__header {
      padding: 0 2rem;
      height: 4rem;

      @media (max-width: 576px) {
        padding: 0 .7rem;
      }
    }
  }

  &-front {
    z-index: 2;
    transform: rotateX(0deg);

    &__header {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      background-color: $colorHeader;
    }
  }

  &-back {
    transform: rotateX(-180deg);
    //position: relative;
  }
}

.search-filter-block {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 1rem;
}

.hide {
  display: none;
}

.modal-animate {

  &-enter {
    opacity: 0;
  }
  &-enter-active {
    opacity: 1;
    transition: opacity 700ms;
  }
  &-exit {
    opacity: 1;
  }
  &-exit-active {
    opacity: 0;
    transition: opacity 700ms;
  }
}

.animate-body {

  &-enter {
    top: -100%;
  }
  &-enter-active {
    top: 0;
    transition: top 500ms;
  }
  &-exit {
    top: 0;
  }
  &-exit-active {
    top: -100%;
    transition: top 500ms;
  }
}







