.form {  display: flex;  align-items: center;  &-add-task {    input {      border-radius: 3px;      background: rgba(0,0,0,.1);    }    .error-label {      color: blue!important;    }  }  &-todo-title {    padding: 0 .7rem;    .error-label {      color: blue!important;    }    .wrap-field {      width: auto;      input {        border: none;        border-radius: 3px;        width: 350px;        outline: none;        background: rgba(0,0,0,.1);        @media (max-width: 576px) {          width: 250px;        }        @media (max-width: 450px) {          width: 140px;        }      }    }    .icon {      flex-grow: 0;    }  }  &-edit-task {    margin-right: .5rem;    input {      border-radius: 3px;      background-color: rgba(0,0,0,.5);      color: yellow;    }    .btn {      border-radius: 0 3px 3px 0;      background-color: #07a107;    }  }  &-add-todo {    border-radius: 3px;    input {      padding: .5rem;      border: none;      border-radius: 3px 0 0 3px;      box-shadow: 0 0 1px 1px #e7e6e6 inset;    }    button {      border: none;      background-color: rgba(7,161,7, .5);      padding: .5rem;      border-radius: 0 3px 3px 0;      &:hover {        background-color: rgba(73,151,73, .8);        &:active {          background-color: rgba(7,161,7, 1);        }      }    }    .error-label {      bottom: -15px;      font-size: .8rem;    }  }  .btn {    padding: .5rem .8rem;    @media (max-width: 576px) {      padding: .3rem .5rem;    }  }  .icon {    margin-left: .5rem;  }  .wrap-field {    width: 100%;    &.error {      position: relative;      & > input {        animation: errorInputAnimation  1s infinite ease-in-out;      }    }  }  .error-label {    color: red;    position: absolute;    bottom: 0;    left: .3rem;    font-size: .7rem;    letter-spacing: .7px;  }  input {    border: none;    padding: .5rem .8rem;    outline: none;    width: 100%;    transition: all .3s ease;    @media (max-width: 576px) {      padding: .3rem .5rem;    }  }}@keyframes errorInputAnimation {  from {    box-shadow: 0 0 5px 1px red inset;  }  50% {    box-shadow: 0 0 1px 1px red inset;  }  to {    box-shadow: 0 0 5px 1px red inset;  }}