  url('../fonts/icomoon.ttf?xpsjjk') format('truetype'),
  url('../fonts/icomoon.woff?xpsjjk') format('woff'),
  url('../fonts/icomoon.svg?xpsjjk#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
