@import "../../assets/styles/variables";@import "../../assets/styles/Roboto";.todos-back__header {  position: absolute;  top: 0;  left: 0;  width: 100%;  background-color: $colorHeader;  display: flex;  justify-content: space-between;  align-items: center;  &-title{    display: flex;    align-items: center;    margin: 0 auto;    cursor: pointer;    font-size: .7rem;    padding: 0 1rem;    @media (max-width: 576px) {      width: 60vw;      justify-content: center;    }    h1 {      @media (max-width: 576px) {        font-size: 1rem;        text-overflow: ellipsis; //образка длинных блоков с текстом        overflow: hidden; //прятать выходящий за пределы текст        //width: 100%; //ширина блока с текстом        white-space: nowrap; //запрет на перенос текста      }    }  }  .icon-pencil {    margin-left: 1rem;  }  &-enter {    transform: scale(0);  }  &-enter-active {    transform: scale(1);    transition: transform 200ms ease;  }  &-exit {    transform: scale(1);  }  &-exit-active {    transform: scale(0);    transition: transform 200ms ease;  }}