@import '../../assets/styles/variables';.btn {  padding: .3rem 1rem;  border-radius: 3px;  border: none;  cursor: pointer;  outline: none;  transition: all .1s ease-in-out;  background-color: $bgColorBtn;  @media (max-width: 576px) {    padding: .3rem!important;  }  &-group {    .btn {      border-radius: 0;      &:first-child {        border-radius: 3px 0 0 3px;      }      &:last-child {        border-radius: 0 3px 3px 0;      }    }  }}