@import '../../assets/styles/variables';.todos-list__item {  margin-bottom: .3rem;  color: rgba(239,255,0,.9);  transition: all .3s ease;  padding: .5rem .8rem;  border-radius: 3px;  background-color: $colorMain;  display: flex;  align-items: center;  justify-content: space-between;  cursor: pointer;  align-self: stretch;  position: relative;  @media (max-width: 576px) {    padding: .2rem .3rem;  }  &-content {    padding-right: .8rem;    text-overflow: ellipsis; //образка длинных блоков с текстом    overflow: hidden; //прятать выходящий за пределы текст    width: 100%; //ширина блока с текстом    white-space: nowrap; //запрет на перенос текста  }  &-info {    @media (max-width: 576px) {      display: flex;      flex-direction: column;    }  }  .todo-title {    color: darkturquoise;    font-weight: 700;    //white-space: nowrap;  }  .sum-tasks {    color: burlywood;  }  &.item-enter {    //left: -100%;    transform: scale(0);  }  &.item-enter-active {    //left: 0;    transform: scale(1);    transition: all 500ms ease-in;  }  &.item-exit {    //left: 0;    transform: scale(1);  }  &.item-exit-active {    //left: 100%;    transform: scale(0);    transition: all 500ms ease-in;  }  &:hover {    background-color: $colorMainHover;    padding-left: 1.5rem;    @media (max-width: 576px) {      padding-left: .5rem;    }  }}