@import '../../assets/styles/variables';.checkbox-custom {  &__input {    display: none;    &:checked + label:before {      transform: scale(1);    }  }  &__label {    cursor: pointer;    position: relative;    width: $widthCheckbox;    height: $widthCheckbox;    display: flex;    align-items: center;    justify-content: center;    @media (max-width: 576px) {      width: 1rem;      height: 1rem;    }    &:after {      content: '';      position: absolute;      width: 100%;      height: 100%;      //border: 1px solid $colorHeader;      border-radius: 50%;      background-color: yellow;      z-index: 1;    }    &:before {      transition: all .2s linear;      content: '';      position: absolute;      width:  80%;      height: 80%;      transform: scale(0);      border-radius: 50%;      background-color: $colorHeader;      z-index: 2;    }  }}