@import '../../assets/styles/variables';.todos-task {  display: flex;  justify-content: space-between;  align-items: center;  margin-bottom: .3rem;  background-color: $colorMain;  padding: .3rem .8rem;  border-radius: 3px;  transition: all .5s ease;  @media (max-width: 576px) {    padding: .3rem .2rem;    font-size: .8rem;  }  &:hover {    background-color: $colorMainHover;    .todos-task-left-block {      transform: translateX(10px);      transition: transform .5s ease;      @media (max-width: 576px) {        transform: translateX(5px);      }    }  }  &.item-enter {    transform: scale(0);  }  &.item-enter-active {    transform: scale(1);    transition: all 200ms ease-in-out;  }  &.item-exit {    transform: scale(1);  }  &.item-exit-active {    transform: scale(0);    transition: all 200ms ease-in-out;  }  &.done {    opacity: .5;  }  &-left-block {    display: flex;    align-items: center;    transform: translateX(0);    transition: all .5s ease;    overflow: hidden;    margin-right: 10px;    padding: .3rem .5rem;    @media (max-width: 576px) {      padding: .1rem .2rem;    }    .task-title {      display: block;      margin-left: .5rem;      text-overflow: ellipsis; //образка длинных блоков с текстом      overflow: hidden; //прятать выходящий за пределы текст      white-space: nowrap; //запрет на перенос текста      width: 100%; //ширина блока с текстом      color: yellow;      &.important {        font-weight: bolder;        color: yellow;        letter-spacing: .5px;        text-transform: uppercase;        @media (max-width: 576px) {          letter-spacing: 0;          font-weight: bold;        }      }      &.done {        text-decoration: line-through;      }    }  }  &.task-empty {    justify-content: center;    letter-spacing: 1px;    text-transform: uppercase;    font-weight: bolder;    padding: 1rem;    background-color: rgba(0, 133, 237, 0.83);    @media (max-width: 576px) {      padding: .5rem;    }  }}