.search {
  @media (min-width: 577px) {
    width: 30vw;
  }

  @media (max-width: 576px) {
    flex-grow: 1;
  }

  input {
    width: 100%;
    padding: .5rem;
    border-radius: 3px;
    box-shadow: 0 0 3px 1px yellow;
    background-color: rgba(0,0,0,.5);
    color: yellow;

    @media (max-width: 576px) {
      padding: .3rem;
    }
  }

  .icon-search {
    color: darkcyan;
    position: absolute;
    top: 0;
    left: 0;
  }

  &-enter {
    opacity: 0;
    width: 0;
  }
  &-enter-active {
    width: 100%;
    opacity: 1;
    transition: all 500ms ease;
  }
  &-exit {
    width: 100%;
    opacity: 1;
  }
  &-exit-active {
    opacity: 0;
    width: 0;
    transition: all 500ms ease;
  }
}