@import "../../assets/styles/variables";.icon {  cursor: pointer;  transform: scale(1);  transition: box-shadow .2s ease, opacity .2s ease, transform .2s ease;  padding: .5rem;  box-shadow: 0 0 3px 1px;  border-radius: 3px;  opacity: .8;  @media (max-width: 576px) {    padding: .4rem;    font-size: .9rem;  }  &-group {    display: flex;    .icon {      display: block;      min-width: 2rem;      text-align: center;      margin: .7rem .5rem .5rem;      @media (max-width: 576px) {        margin: .2rem;      }      &-pencil {        color: #576ce8;      }    }  }  &.active,  &:hover {    opacity: 1;    box-shadow: 0 0 3px 3px;    transform: scale(1.1);    @media (max-width: 576px) {      box-shadow: 0 0 1px 2px;    }  }  &-exclamation {    color: #07a107;  }  &-trash-o {    color: red;  }}