@import '../../assets/styles/variables';.modal {  position: fixed;  top: 0;  left: 0;  right: 0;  bottom: 0;  background-color: $colorBackground;  z-index: 100;  display: flex;  flex-direction: column;  justify-content: center;  align-items: center;    &-wrapper {    position: relative;  }  &-body {    padding: 2rem;    background-color: $colorWhite;    border-radius: 3px;    color: black;    min-width: 300px;  }  &-header {    font-size: .8rem;    margin-bottom: .8rem;    @media (max-width: 576px) {      font-size: .7rem;    }  }  &-close {    position: absolute;    top: -10px;    right: -10px;    color: $colorRed;    font-size: 18px;    background-color: black;    &:hover {      transform: scale(1.1);    }    &.icon-close {      @media (max-width: 576px) {        top: 5px;        right: 5px;        background: none;        box-shadow: none;        padding: 0;      }      &:hover {        @media (max-width: 576px) {          box-shadow: none;        }      }    }  }}